.tui-pagination * {
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }
  
  .tui-ico-ellipsis,
  .tui-ico-first,
  .tui-ico-last,
  .tui-ico-next,
  .tui-ico-prev {
    overflow: hidden;
  }
  
  .tui-pagination {
    height: 40px;
    margin: 40px 0;
    line-height: normal;
    text-align: center;
    font-size: 0;
  }
  
  .tui-pagination .tui-page-btn {
    display: inline-block;
    position: relative;
    width: 30px;
    padding: 8px 0 6px;
    margin-left: 10px;
    color: $mainColor;
    font-size: 14px;
    font-weight: 500;
    line-height: 1;
    text-decoration: none;
    vertical-align: middle;
    border-radius: 50%;
  
    &:hover,
    &:focus {
      border: 1px solid $accentColor;
    }
  }
  
  .tui-pagination .tui-is-selected,
  .tui-pagination strong {
    color: #ffffff;;
    background: $accentColor;
    border: 1px solid $accentColor;
    cursor: default;
  }
  
  .tui-pagination .tui-first-child.tui-is-selected,
  .tui-pagination strong.tui-first-child {
    margin-left: 20px;
  }
  
  .tui-pagination .tui-first-child.tui-is-selected {
    border-left: 1px solid $accentColor;
  }
  
  .tui-pagination .tui-first,
  .tui-pagination .tui-prev,
  .tui-pagination .tui-next,
  .tui-pagination .tui-last,
  .tui-pagination .tui-prev-is-ellip,
  .tui-pagination .tui-next-is-ellip {
    overflow: hidden;
    height: 26px;
    padding: 0;
    font-size: 0;
    line-height: 26px;
    white-space: nowrap;
    font-weight: normal;
  }
  
  .tui-pagination .tui-prev-is-ellip,
  .tui-pagination .tui-next-is-ellip {
    border: none;
  
    &:hover,
    &:focus {
      border: none;
    }
  }
  
